<template>
  <div class="login">
   <div class="container">
       <form @submit.prevent="login">
           <input type="text" placeholder="user name" v-model="userName" required />
           <input type="password" placeholder="passowrd" v-model="password" required />
           <input type="submit" value="login" />
       </form>
       <button @click="logout">xlogout</button>
       <button @click="testAuth">Auth Test</button>
   </div>
  </div>
</template>

<script lang="ts">
import { Options, Vue } from "vue-class-component";
import { AuthService } from "@/core/services/AuthService";
@Options({
  components: {

  },
})
export default class Login extends Vue {
    private userName: string = '';
    private password: string = '';

     private async login() {
        await AuthService.login(this.userName, this.password)

        var isLoggedIn = await localStorage.getItem("user");
        if (isLoggedIn) {
            console.log("loggedIn")
            this.$router.push("/")
        }
        else {
            alert("incorrect")
           
        }
       // console.log(`token:${token}`);
    }

    private logout() {
        AuthService.logout();
    }

    private testAuth() {
        AuthService.getAll();
    }
}
</script>
